.modals {
	display: none;
}

.tingle-modal {
	background: rgba($color__white, .5);
	pointer-events: all !important;
	z-index: 100;

	* {
		pointer-events: all !important;
	}

	&--overflow {
		padding-bottom: 8vh;
	}

	.tingle-modal-box {
		box-shadow: $shadow;
		margin-left: auto;
		margin-right: auto;

		.modal-close {
			@include position(absolute, 0 20px null null);

			.hamburger {

				&__top,
				&__middle,
				&__bottom {
					width: 34px;
				}

				&__top {
					top: 13px;
					transform: rotate(45deg);
				}

				&__middle {
					opacity: 0;
				}

				&__bottom {
					top: 13px;
					transform: rotate(-45deg);
				}
			}

			+ * {
				padding-right: 20px;
			}
		}

		form {
			margin-bottom: 20px;
		}
	}

	&.small-modal {

		.tingle-modal-box {
			max-width: 400px;
		}
	}
}



